




















































































































































































































































































































































































































































































































@use "~main/theme"

.wrapper
  &:not(.is-in-mobile)
    margin: 2.5rem 0

    @media (max-width: 768px)
      margin: 0

  .desktop-settings-wrapper
    background-color: rgb(var(--color-surface-secondary))

    @media (max-width: 768px)
      display: none

    ::v-deep .desktop-settings-content
      padding: 2rem
      padding-right: 3rem

  .mobile-settings-wrapper
    padding: 2rem 0

    @media (min-width: 769px)
      display: none

    .mobile-settings-content
      background-color: rgb(var(--color-surface-secondary))
      padding: 1.5rem 2.5rem 1.5rem 1.5rem

      &.is-in-mobile
        padding: 0 1.5rem

    .mobile-settings-categories
      .categories-title
        font-weight: bold
        font-size: 1.5rem
        margin-bottom: 1rem

      a
        color: rgb(var(--text-primary))
        display: flex
        justify-content: space-between
        align-items: center
        padding: .8rem 0
        border-bottom: 1px solid rgb(var(--color-divider))

        i
          font-size: .8rem

      .preferences-category-title
        margin: 0
        font-size: 1rem
        line-height: 1.5
        padding: 1.5rem 0 .8rem
        border-bottom: 1px solid rgb(var(--color-divider))
        color: rgb(var(--color-text-secondary))

::v-deep section:not(:first-child) .section-title
  margin-top: 2rem

::v-deep .section-title
  font-weight: bold
  font-size: 1.5rem
  margin-bottom: 1rem
  border-bottom: 1px solid rgb(var(--color-text-primary))

::v-deep .settings-nav
  width: 25%
  background-color: rgb(var(--color-surface-primary))
  min-height: 80vh
  padding: 1rem 0

  a
    border: initial

::v-deep .nav-tabs
  border-bottom: none
