












































































































































::v-deep .navbar
  padding: 0.5rem 1rem

  a.navbar-brand
    margin-top: -3px

    img
      width: 232px

  .nav-item
    text-transform: uppercase

  .user-menu
    text-transform: none

  .badge-btn
    position: relative
    height: 1.8rem
    margin-right: 1.5em

    img
      height: 1.3rem

    .badge
      position: absolute
      left: 22px
      top: 1px
      height: 18px
      border-radius: 4px
      transition: transform 0.2s

      /* Animation */
      &:hover
        transform: scale(1.3)

  @media (min-width: 1200px)
    padding: 0rem 1rem

    .nav-item
      padding: 0.5rem 0.24rem
